import React, { useCallback } from 'react';
import { useHistory } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import { Breadcrumb, ButtonCustom, TitleCustom } from 'src/components/base';

import PromotionItem from 'src/components/base/layout/PromotionItem';

import './promotion.scss';

const PromotionScreen = (props) => {
  const history = useHistory();

  const handleOnClickItem = useCallback((item) => {
    history.push(`/promotion/${item.id}`, { item });
  }, []);

  return (
    <div className="promotion-container">
      <Breadcrumb items={[{ name: 'Khuyến mãi', url: '/promotion' }]} />

      {/* <Grid container lg={12} md={12} justifyContent="center">
        <Slider onClickSlider={() => {}} data={props.listBanner} />
      </Grid> */}
      <div className="promotion_layout">
        <div style={{ marginTop: '20px' }} className="promotion_content">
          <div style={{ marginBottom: '36px' }}>
            <TitleCustom title="Khuyến mãi" />
          </div>

          {props.listPromotion?.data?.map((obj, idx) => {
            const flagHiddenLine = props.listPromotion.data.length - 1 === idx;
            return (
              <div style={{ marginBottom: '16px' }}>
                <PromotionItem
                  key={obj?.id}
                  data={obj}
                  hideLine={flagHiddenLine}
                  onClickItem={handleOnClickItem}
                />
              </div>
            );
          })}
          {props.listPromotion?.data?.length < props.listPromotion.total && (
            <Grid container className="box-button">
              <ButtonCustom
                onClick={props.onLoadMoreClick}
                item
                title="XEM THÊM"
                customStyle="custom-button"
              />
            </Grid>
          )}
        </div>
      </div>
    </div>
  );
};

export default React.memo(PromotionScreen);
