import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getListPromotionHandle } from 'src/redux/actions/promotion.action';
import { getListBannerSelector } from 'src/redux/selectors/common.selector';
import PromotionView from './promotion.view';

const PromotionContainer = () => {
  const [listPromotion, setListPromotion] = useState([]);
  const dispatch = useDispatch();
  const listBanner = useSelector(getListBannerSelector);
  useEffect(() => {
    dispatch(
      getListPromotionHandle(1, 10, (listPromotionRes) => {
        console.log('listPromotionRes', listPromotionRes);
        setListPromotion({
          ...listPromotionRes,
          total: listPromotionRes.metadata.total,
          page: 1,
          size: 10,
        });
      })
    );
  }, []);
  return (
    <PromotionView
      onLoadMoreClick={() => {
        dispatch(
          getListPromotionHandle(
            listPromotion.page + 1,
            listPromotion.size,
            (promotions) => {
              setListPromotion({
                ...promotions,
                data: listPromotion.data.concat(promotions.data),
                page: listPromotion.page + 1,
                size: listPromotion.size,
                total: promotions.metadata.total,
              });
            }
          )
        );
      }}
      listBanner={listBanner}
      listPromotion={listPromotion}
    />
  );
};

export default PromotionContainer;
