import React, { useCallback } from 'react';
import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import { noImage } from 'src/assets/images';
import './styles.scss';

function PromotionItem(props) {
  const { data, onClickItem = () => {}, hideLine } = props;
  const { imageCover = noImage, description = '', name = '' } = data || {};

  const handleOnClick = useCallback(() => {
    if (onClickItem && typeof onClickItem === 'function') {
      onClickItem(props.data);
    }
  }, []);

  return (
    <Grid onClick={handleOnClick} item>
      <div className="item-info-container">
        <Grid container spacing={3}>
          <Grid item lg={3} xl={3} md={3} sm={12}>
            <img className="img" alt="" src={imageCover} />
          </Grid>
          <Grid item xl={9} lg={9} md={7} sm={8}>
            <p className="promotion_name">{name}</p>
            <div
              className="promotion_description"
              dangerouslySetInnerHTML={{ __html: description }}
            />
          </Grid>
        </Grid>

        {!hideLine && <div className="line" />}
      </div>
    </Grid>
  );
}

PromotionItem.propTypes = {
  onClickItem: PropTypes.func.isRequired,
};

export default React.memo(PromotionItem);
